    background: transparent
        url("https://thinkfuture.com/wp-content/uploads/2013/10/loading_spinner.gif")
        center no-repeat;
}

p {
    font-size: large !important;
}

#abt_nav:focus,
#abt_contact:focus {
    border-color: unset;
    outline: 0;
}

#abt_contact {
    z-index: 1;
    background: transparent;
}



iframe#abt_twitter-widget-0 {
    margin-top: 37px !important;
}

ul {
    list-style-type: none !important;
}

#abt_main_body {
    font-family: "Laila", serif !important;
    margin: 2vw;
    margin-top: 6.3vw;
    display: flex;
    justify-content: center;
}

#abt_avatar {
    float: left;
    height: 60vmin;
    width: 60vmin;
    border-radius: 26vw;
    margin-right: 1vw;
}

#abt_feed {
    height: 79vh;
    overflow-y: scroll;
    overflow-x: hidden;
}

#abt_heading {
    font-size: 3vmin;
    position: fixed;
    border-bottom: 1px dotted #9e9e9e;
}
