    padding-left: 0px !important;
}

img {
    height: 4vmin;
}

i {
    padding-right: 6px;
}

.fab {
    font-size: 5vmin;
}

#pro_nav:focus,
#pro_contact:focus {
    border-color: unset;
    outline: 0;
}

.medium-title > .sideByside {
    border-bottom: 4px solid #d3c69c;
}

#pro_head {
    background: black;
}

#pro_contact {
    z-index: 1;
    background: transparent;
}

#pro_projectsContainer {
    margin-top: 11vmin;
    margin-left: 2vmin;
    margin-right: 2vmin;
    margin-bottom: 2vmin;
}

#pro_projectsContainer,
#pro_contents,
#pro_project_title {
    font-family: 'Laila', serif !important;
}

.transDiv {

    display: inline-flex;
    align-items: center;
}

.transDiv > p {
    display: contents;
}

.transArrow {
    transform: rotate(-90deg);
}

#pro_heading {
    display: inline-flex;
    width: 100vw;
    text-align: center;
    align-items: center;
}

.tab {
    background-color: #edeff0;
}

h2 {
    font-family: inherit !important;
    border-bottom: 1px solid black;
    width: max-content;
}

.tablinks {
    font-size: large !important;
}

.tab button {
    display: inline-flex;
    justify-content: center;
    background-color: inherit;
    /*float: left;*/
    border: none;
    outline: none;
    cursor: pointer;
    padding: 14px 16px;
    transition: 0.3s;
    /*font-size: 17px;*/
    font-weight: 900;
    color: black;
    margin: .3vmin;
    z-index: 1;
}

.tab button:hover {
    background-color: #ddd;
}

.tab button.active {
    background-color: wheat;
    color: black;
}

.tabcontent {
    display: none;
    padding: 6px 12px;
    border-top: none;
    color: black;
    font-size: 2vmin;
    width: 100%;
}

#pro_ProjectsContent {
    display: inline-flex;
}

.box {
    border-bottom-style: double;
    border-bottom-color: dimgray;
    z-index: -1;
}

#pro_project_title {
    font-size: 4vmin;
}

.sideByside {
    display: inline-flex;
    padding: 4vmin;
    width: 100%;
}

pre,
code {
    direction: ltr;
    text-align: left;
}

pre {
    border: solid 1px #6c757d;
    font-size: 1.3em;
    color: blue;
    background: #ced4da;
}

code {
    font-size: 1.2em;
    color: #008099
}

* {
    box-sizing: border-box
}

body {
    font-family: Verdana, sans-serif;
    margin: 0
}

img {
    vertical-align: middle;
}

[class^="slideshow-container"] {
    max-width: 100%;
    position: relative;
    margin: auto;
    width: auto;
}

.centered {
    position: absolute;
    top: 50%;
    left: 52% !important;
    transform: translate(-50%, -50%);
}

.prev,
.next {
    cursor: pointer;
    position: absolute;
    top: 50%;
    width: auto;
    padding: 16px;
    margin-top: -22px;
    color: white;
    font-weight: bold;
    font-size: 18px;
    transition: 0.6s ease;
    border-radius: 0 3px 3px 0;
}

.next {
    right: 0;
    border-radius: 3px 0 0 3px;
}

.prev:hover,
.next:hover {
    background-color: rgba(0, 0, 0, 0.8);
}

.text {
    color: #f2f2f2;
    padding: 8px 12px;
    position: absolute;
    bottom: 21px;
    text-align: center;
    display: flex;
    justify-self: anchor-center;
    text-shadow: 1px -1px black;
    font-weight: bolder;
}

.numbertext {
    color: #f2f2f2;
    font-size: 12px;
    padding: 8px 12px;
    position: absolute;
    top: 0;
}

[class^="dot"] {
    cursor: pointer;
    height: 15px;
    width: 15px;
    margin: 0 2px;
    background-color: #bbb;
    border-radius: 50%;
    display: inline-block !important;
    transition: background-color 0.6s ease;
}

.active,
[class^="dot"]:hover {
    background-color: #717171;
}

.fade {
    -webkit-animation-name: fade;
    -webkit-animation-duration: 1.5s;
    animation-name: fade;
    animation-duration: 1.5s;
}

@-webkit-keyframes fade {
    from {
        opacity: .4
    }
    to {
        opacity: 1
    }
}

@keyframes fade {
    from {
        opacity: .4
    }
    to {
        opacity: 1
    }
}

@media only screen and (max-width: 300px) {
    .prev,
    .next,
    .text {
        font-size: 11px
    }
}

.proWIP {
    height: 21vmin;
    width: 24vmin;
}


.toggleMenu {
    width: 100%;
    background-color: wheat;
    color: black !important;
}


ul {
    list-style-type: none !important;
}

.centered {
    position: absolute;
    top: 50%;
    transform: translate(-50%, -50%);
}

.sideByside > .col-sm-6 {
    display: flex;
    justify-content: center;
    font-size: large;
}

#pro_content {
    justify-content: center;
    width: inherit;
    background: local !important;
    display: grid;
}

#pro_jumbo {
    font-size: 9vmin;
}

#pro_WIP {
    height: 30vw;
    width: 28vw;
    transform: rotate(84deg);
}

.bottom-right {
    position: absolute;
    bottom: 5vmin;
    right: 16px;
}

#pro_slide {
    position: absolute;
    left: 0vw;
    top: 50vh;
    /*float: left;*/
    width: 20.5vw;
    border-left: 1px solid #000;
    border-right: 1px solid #000;
    transform: rotate(90deg);
    box-sizing: border-box;
    -moz-box-sizing: border-box;
    -webkit-box-sizing: border-box;
}

p.medium-feed-image, figure {
    display: none;
}

#pro_bulb {
    height: 2.5vh;
    width: 7vw;
    border-radius: 3vmin;
    background-image: linear-gradient(to right top, #0e3705, #195f15, #218b27, #22ba3c, #12eb54);
    border: 1px solid slategray;
}

.h4,
h4 {
    font-size: 2.5vmin !important;
}

.project-slide:hover,
.project-slide:active {
    height: 15vh;
}

.project-slide {
    height: 3.5vh;
    cursor: pointer;
    overflow: hidden;
    text-align: center;
    background: linear-gradient(to left bottom, #f5deb3, #d9cba0, #bdb88e, #a2a67e, #88936e);
    font-size: 2vmin !important;

    transition: height .4s ease-in-out;
    -webkit-transition: height .4s ease-in-out;
    -moz-transition: height .4s ease-in-out;

}

#pro_all_projects {
    /*float: left;*/
    display: inline-flex;
    justify-content: center;
    height: 5.5vmin;
    width: 22vmin;
    align-items: center;
    border: 1px solid #9E9E9E;
    cursor: pointer;
    font-family: 'Laila', serif !important;
}

.grow:hover {
    -webkit-transform: scale(1.3);
    -ms-transform: scale(1.3);
    transform: scale(1.3);
}

.logos {
    animation: shake 0.82s cubic-bezier(.36, .07, .19, .97) both;
    transform: translate3d(0, 0, 0);
    backface-visibility: hidden;
    perspective: 1000px;
}

@keyframes shake {
    10%,
    90% {
        transform: translate3d(-1px, 0, 0);
    }

    20%,
    80% {
        transform: translate3d(2px, 0, 0);
    }

    30%,
    50%,
    70% {
        transform: translate3d(-4px, 0, 0);
    }

    40%,
    60% {
        transform: translate3d(4px, 0, 0);
    }
}
